.welcome-section-container {
  height: 100vh;
  overflow: hidden;
  position: relative;
  background-image: url("./ocean2.png");
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.welcome-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  top: 35%;
}

.welcome-container .text-container {
  color: #f2f2f2;
  padding-bottom: 55%;
  position: relative;
}
.welcome-container h1 {
  opacity: 0;
  margin-bottom: 5px;
  font-family: "Chronicle Display Bold", Georgia, serif;
  font-weight: lighter;
  font-size: 55px;
  animation: text1 0.4s ease-in 0.6s forwards;
  position: relative;
}

.text-container p {
  margin-top: 5px;
  font-size: 20px;
  font-family: "Karla", sans-serif;
  animation: text2 0.4s ease-in 0.6s forwards;
  opacity: 0;
  position: relative;
}

.text-container .line-container {
  opacity: 1;
  display: flex;
  justify-content: center;
}

.text-container .line-container div {
  width: 0%;
  animation: linehr 1.2s ease-in-out alternate-reverse forwards;
}
.subtext-container {
  cursor: pointer;
  text-decoration: none;
  margin-bottom: 20px;
}

.subtext-container p {
  color: #f2f2f2;
  font-family: "Karla", sans-serif;
}

.subtext-container span {
  position: relative;
  animation-name: example;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes example {
  0% {
    top: 0px;
  }

  50% {
    top: 2px;
  }

  100% {
    top: 0px;
  }
}

/* ----------ANIMATIONS---------- */

@keyframes text1 {
  0% {
    opacity: 0;
    bottom: -30px;
  }
  50% {
    bottom: 0;
    opacity: 1;
  }
  100% {
    bottom: 0;
    opacity: 1;
  }
}
@keyframes text2 {
  0% {
    opacity: 0;
    top: -30px;
  }

  50% {
    top: 0;
    opacity: 1;
  }
  100% {
    top: 0;
    opacity: 1;
  }
}

@keyframes linehr {
  0% {
    opacity: 0;
    width: 0%;
  }

  20% {
    opacity: 1;
    width: 100%;
  }
  50% {
    width: 100%;
  }
  99% {
    opacity: 1;
  }
  100% {
    width: 0%;
    opacity: 0;
  }
}

/* ------MEDIA QUERIES ------*/
@media (max-width: 800px) {
  .subtext-container {
    margin-bottom: 70px;
  }
}
@media (max-width: 450px) {
  .welcome-container .text-container {
    position: relative;
    bottom: 20%;
  }
  .welcome-container .text-container h1 {
    font-size: 100px;
    margin-top: 0;
  }
  .welcome-container .text-container p {
    font-size: 40px;
  }
  .subtext {
    font-size: 30px;
  }
}
