.cv-container {
  width: 100%;
  min-height: 100px;
  overflow: hidden;
  color: #f2f2f2;
  position: relative;
  background: #00909c;
  font-family: "Quicksand", sans-serif;
  font-size: 1em;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
  overflow: hidden;
  transition: background-color 0.5s linear;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.cv-container a {
  width: 100%;
  height: 100%;
  color: #f2f2f2;
}
.onHover {
  background: #00b7c7;
}
.cv-container img {
  width: 50px;
}
@media (max-width: 660px) {
  .cv-container p {
    font-size: 1.2em;
  }
}

@media (max-width: 450px) {
  .cv-container p {
    font-size: 1.7em;
  }
}
