.about-section-container {
  min-height: 700px;
  background-color: #1b1b1b;
}

.about-container {
  padding: 100px;
}

.about-content {
  color: #f2f2f2;
}

.about-content h1 {
  font-size: 3em;
  font-family: "Chronicle Display Bold", Georgia, serif;
  font-weight: lighter;
  text-align: left;
}

.about-content h3 {
  width: 60%;
  font-size: 1.2em;
  font-weight: 500;
  line-height: 1.5;
  margin-top: 1em;
  font-family: "Karla", sans-serif;
  text-align: left;
}
.about-content .line-container {
  width: 10%;
}
.about-content .line-container hr {
  border: #00b7c7 solid 1px;
}
.about-wrapper {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

.about-text {
  font-size: 1.15em;
  line-height: 1.5em;
  margin-bottom: 10px;
  flex: 0.6;
  margin-right: 80px;
  text-align: left;
}

.about-img {
  flex: 0.4;
  width: 100%;
  max-width: 650px;
  position: relative;
  bottom: 90px;
}
.about-img img {
  width: 100%;
  border-radius: 10%;
}

@media (max-width: 1400px) {
  .about-img {
    bottom: 0px;
  }
  .about-content h3 {
    width: 100%;
  }
}

@media (max-width: 1100px) {
  .about-container {
    padding: 70px;
  }
  .about-wrapper {
    flex-direction: column;
  }
  .about-img {
    position: unset;
    margin: 20px 0;
    width: 55%;
  }
}

@media (max-width: 900px) {
  .about-container {
    padding: 100px 50px;
  }
  .about-wrapper {
    flex-direction: column;
  }
  .about-img {
    position: unset;
    margin: 20px 0;
    width: 70%;
  }
}

@media (max-width: 700px) {
  .about-img {
    width: 500px;
  }
}

@media (max-width: 450px) {
  .about-container {
    padding: 100px 25px;
  }
  .about-content h1 {
    font-size: 75px;
  }
  .about-content h3 {
    font-size: 38px;
  }
  .about-content .line-container {
    margin-bottom: 30px;
  }
  .about-content .about-text {
    font-size: 36px;
  }
  .about-content .line-container {
    width: 30%;
  }
}
