.works-section-container {
  min-height: 700px;
  background-color: #1b1b1b;
}

.works-container {
  padding: 100px;
}

.works-content {
  color: #f2f2f2;
}

.works-content h1 {
  font-size: 3em;
  font-family: "Chronicle Display Bold", Georgia, serif;
  font-weight: lighter;
  text-align: left;
}

.works-content .line-container {
  width: 10%;
}
.works-content .line-container hr {
  border: #00b7c7 solid 1px;
}
.images-container {
  position: relative;
  overflow: hidden;
  display: block;
  width: 95%;
  margin-left: 10px;
  margin-top: 7%;
}
.images-wrap {
  display: flex;
  align-items: baseline;
  justify-content: flex-start;
}
.images-container .img-lg {
  position: relative;
  width: 70%;
  left: 0;
  z-index: 1;
}
.images-container .img-md {
  position: relative;
  top: 9px;
  width: 27%;
  right: 14%;
  z-index: 2;
}
.images-container .img-sm {
  position: relative;
  top: 10px;
  width: 16.5%;
  right: 20%;
  z-index: 3;
}
.description-title-container {
  position: relative;
  width: 88%;
  margin-top: 30px;
  margin-left: 1em;
  align-items: baseline;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: flex-end;
  align-content: stretch;
}
.description-title-container h2 {
  flex: 1;
  margin-bottom: 20px;
  font-family: "Karla", sans-serif;
  text-transform: uppercase;
  text-align: left;
}
.description-buttons a {
  padding: 10px 15px;
  background: 0 0;
  border: 2px solid #00b7c7;
  border-radius: 3px;
  margin: 0 5px;
  color: #00b7c7;
  font-family: "Karla", sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  transition: all 0.3s ease;
}
.description-content {
  display: block;
  width: 88%;
  margin-top: 15px;
  margin-left: 1rem;
  margin-bottom: 40px;
  font-size: 1.1em;
  color: #cbcbcb;
  text-align: left;
  line-height: 1.5em;
  letter-spacing: 1.2px;
}

.used {
  display: block;
  width: 90%;
  font-family: "Karla", sans-serif;
  margin-top: 30px;
  margin-bottom: 100px;
  text-align: left;
}
.used-items {
  margin-left: 1em;
  padding-left: 0;
}
.used-item {
  display: inline;

  padding: 7px 7px 5px;
  margin-right: 5px;
  margin-bottom: 10px;
  color: #777;
  background: #333;
  border-radius: 3px;
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 500;
  letter-spacing: 0.5px;
}

#covid-md {
  position: relative;
  top: 20px;
}
#covid-sm {
  position: relative;
  top: 27px;
}

@media (max-width: 900px) {
  .works-container {
    padding: 100px 50px;
  }
}

@media (max-width: 660px) {
  .works-container {
    padding: 100px 25px;
  }
  .description-title-container h2 {
    font-size: 30px;
  }
  .description-content {
    font-size: 1.4em !important;
  }
  .used-items li {
    font-size: 13px;
  }
}
@media (max-width: 600px) {
  .description-title-container {
    flex-direction: column;
  }
  .description-buttons {
    margin: 40px 0;
  }
}
@media (max-width: 450px) {
  .works-content h1 {
    font-size: 90px;
  }
  .works-container .line-container {
    width: 30%;
    margin-bottom: 80px;
  }
  .description-title-container h2 {
    font-size: 42px;
  }
  .description-buttons a {
    font-size: 27px;
  }
  .description-content {
    font-size: 29px !important;
  }
  .used-items li {
    font-size: 20px;
  }
}
