.contact-section-container {
  min-height: 350px;
  background-image: url("./contact1.jpeg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
  color: #f2f2f2;
}
.contact-container {
  padding: 100px 100px 20px 100px;
  min-height: 350px;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61)
  );
  background-image: linear-gradient(
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61)
  );
  background-position-x: initial;
  background-position-y: initial;
  background-size: initial;
  background-repeat-x: initial;
  background-repeat-y: initial;
  background-attachment: initial;
  background-origin: initial;
  background-clip: initial;
  background-color: initial;
  width: 100%;
  height: 100%;
}

.center {
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}
.center h1 {
  font-size: 3em;
  font-family: "Chronicle Display Bold", Georgia, serif;
  font-weight: lighter;
  margin: 0;
}
.contact-container .line-container {
  display: flex;
  justify-content: center;
  margin: 30px;
}
.contact-container .line-container .line {
  width: 10%;
}
.contact-container .line-container hr {
  border: #00b7c7 solid 1px;
}
.center p {
  margin: 0 auto;
  max-width: 440px;
  font-size: 1.2em;
  line-height: 1.5em;
  letter-spacing: 1.2px;
}

.contact-container .contact {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 70px 0;
}

.contact-container .contact-text {
  margin-left: 10px;

  font-family: "Karla", sans-serif;
}

.github-logo {
  width: 30px;
  position: relative;
  top: 5px;
}

.linkedin-logo {
  width: 35px;
  position: relative;
  top: 5px;
}

@media (max-width: 1000px) {
  .contact p {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  p span {
    margin-top: 15px;
  }
}

@media (max-width: 900px) {
  .contact-container {
    padding: 100px 50px 20px 50px;
  }
}
@media (max-width: 750px) {
  .contact {
    flex-direction: column-reverse;
  }
}

@media (max-width: 660px) {
  .center h1 {
    font-size: 60px;
  }
}

@media (max-width: 450px) {
  .contact-container {
    padding: 100px 25px 20px 25px;
  }
  .center h1 {
    font-size: 90px;
  }
  .contact-container .line-container div {
    width: 30% !important;
  }
  .center p {
    font-size: 1.5em;
  }
  .contact {
    font-size: 1.4em;
  }
}
