  color: white;
  position: fixed;
  right: 0px;
  top: 0;
  padding: 35px;
  min-width: 50px;
  transition: linear;
}
.mobile-nav-lines .lines {
  min-width: 50px;
  min-height: 15px;
  border-top: white solid 4px;
  width: 100%;
  transition: all 0.35s ease;
}
#line3 {
  transform: translateY(-10px) translateX(0) rotate(-45deg);
}

#line1 {
  transform: translateY(19px) translateX(-8px) rotate(45deg);
}
#line2 {
  opacity: 0;
  transition: ease-in-out;
}

.mobile-nav-container .sections div {
  display: none;
  z-index: 10;
  position: fixed;
  background: rgba(0, 183, 199, 0.95);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  overflow: hidden;
  z-index: 5;
  transition: all 1s ease;
  flex-direction: column;
  justify-content: flex-start;
}
.active div {
  display: flex !important;
}

.mobile-nav-container .sections a {
  padding: 60px;
  color: white;
  font-size: 40px;
  animation: sections 0.5s ease-in-out forwards;
}
@keyframes sections {
  0% {
    position: relative;
    bottom: 100px;
    opacity: 0;
  }

  100% {
    position: relative;
    bottom: 0px;
    opacity: 1;
  }
}
#news {
  margin-top: 30%;
}

@media (max-width: 770px) {
  .mobile-nav-container {
    display: block;
  }
}

