.services-section-container {
  min-height: 700px;
  background-image: url("./working.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #f2f2f2;
}
.services-container {
  padding: 100px;
  min-height: 700px;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61)
  );
  background-image: linear-gradient(
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61),
    rgba(0, 0, 0, 0.61)
  );
  background-position-x: initial;
  background-position-y: initial;
  background-size: initial;
  background-repeat-x: initial;
  background-repeat-y: initial;
  background-attachment: initial;
  background-origin: initial;
  background-clip: initial;
  background-color: initial;
  width: 100%;
  height: 100%;
}

.services-container h1 {
  line-height: 1.15;
  text-align: left;
  font-size: 3em;
  font-family: "Chronicle Display Bold", Georgia, serif;
  font-weight: lighter;
}
.services-container .line-container {
  width: 10%;
  margin-bottom: 25px;
}
.services-container .line-container hr {
  border: #00b7c7 solid 1px;
}
.services-wrapper {
  display: flex;
  padding: 20px;
}
.service-container {
  padding: 20px;
  text-align: left;
  flex: 0.5;
}
.service-container p {
  line-height: 1.5em;
  font-size: 1.1em;
  font-family: Whitney, Helvetica, Arial, sans-serif;

  white-space: initial;
}
.service-title {
  font-size: 20px;
  margin-left: 20px;
  font-weight: bold;
}

/* --------------------------------------SKILLS------------------------------------- */
.skills-wrapper {
  display: flex;
  padding: 20px;
}
.development-container {
  flex: 0.5;
  padding: 20px;
}
.development-wrapper {
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
}
.skill-title {
  display: flex;
  justify-content: left;
  align-items: center;
}
.skill-title-left {
  justify-content: left;
}

.skill-title h4 {
  margin-left: 10px;
}
.skill-content {
  width: 75px;
  height: 75px;
  border: 2px solid #808080;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin-right: 10px;
  margin-bottom: 10px;
  transition: ease-in 0.2s;
}
.skill-content:hover {
  color: #00b7c7;
  border: #00b7c7 solid 1px;
  transition: ease-in 0.2s;
}
.skill-content p {
  font-size: 10px;
}

/* ------TOOLS---- */

.tools-container {
  flex: 0.5;
  padding: 20px;
}

.tools-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.tool {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2px;
}
.tool span {
  margin-left: 10px;
  font-family: "Quicksand", sans-serif;
}
#last-tool {
  display: none;
}
/* ------KNOWLEDGE---- */

@media (max-width: 1000px) {
  .services-wrapper {
    padding: 0px;
  }
  .skills-wrapper {
    padding: 0px;
  }
}

@media (max-width: 900px) {
  .services-container {
    padding: 100px 50px;
  }
}
@media (max-width: 860px) {
  #last-tool {
    display: flex;
  }
}
@media (max-width: 690px) {
  .development-container {
    padding: 20px 0;
  }
}

@media (max-width: 660px) {
  .tools-container {
    padding: 20px;
  }
  .development-container {
    padding: 20px;
  }
  .services-container h1 {
    font-size: 60px;
  }
  .services-wrapper {
    flex-direction: column;
  }
  .service-container span {
    font-size: 30px;
  }
  .skill-title {
    font-size: 25px;
  }
  .skills-wrapper {
    flex-direction: column;
  }
  #lightroom {
    position: relative;
    left: 4%;
  }
  .skill-content {
    width: 120px;
    height: 120px;
  }
  .skill-content p {
    font-size: 15px;
  }
  .tools-wrapper {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: normal;
  }
  #photoshop {
    position: relative;
  }
  .tool {
    padding: 20px;
  }
  .tool img {
    width: 50px !important;
  }
  .tool span {
    font-size: 20px;
  }
}

@media (max-width: 450px) {
  .services-container {
    padding: 100px 25px;
  }
  .services-container h1 {
    font-size: 90px;
  }
  .services-container .line-container {
    width: 30%;
    margin-bottom: 20px;
  }
  .service-container .title-icon {
    margin-bottom: 50px;
  }
  .service-container span {
    font-size: 40px;
    margin-left: 30px;
  }
  .services-wrapper .fa-2x {
    font-size: 3em;
  }
  .service-container p {
    font-size: 36px;
    font-weight: 300;
  }

  .skill-title {
    font-size: 36px;
  }
  h4 {
    margin-left: 35px !important;
  }
  .skill-content {
    width: 130px;
    height: 130px;
  }
  .skill-content p {
    font-size: 18px;
  }
  .tools-wrapper {
    flex-direction: row;
    justify-content: center;
  }
  .tools-wrapper p {
    display: flex;
    align-items: center;
  }
  #last-tool {
    display: none;
    align-items: center;
  }
  #lightroom {
    position: relative;
    left: 2%;
  }

  .tool {
    padding: 35px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .tool img {
    width: 55px !important;
  }
  .tool span {
    margin-top: 12px;
    font-size: 20px;
  }
}
