#dot-nav {
  position: fixed;
  right: 34px;
  z-index: 1;
  top: 50%;
  bottom: auto;
  transform: translateY(-50%);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.5s ease;
}
#dot-nav.active {
  visibility: visible;
  opacity: 1;
  transition: visibility 0s, opacity 0.5s ease;
}
#dot-nav ul {
  position: relative;
}

#dot-nav li {
  list-style-type: none;
  margin-bottom: 30px;
}
#dot-nav li:hover .dot-label {
  display: inline-block;
}

#dot-nav li a {
  color: #f2f2f2;
  text-decoration: none;
  position: "relative";
}

#dot-nav .dot {
  transition: transform 0.2s, color 0.5s;
  transform-origin: 50% 50%;
  cursor: pointer;
}
#dot-nav .dot-active {
  font-size: 105px;
  color: #00b7c7;
  transition: transform 0.2s, color 0.5s;
  transform-origin: 50% 50%;
  cursor: pointer;
}
#dot-nav .dot-label {
  position: absolute;
  right: 30px;
  display: none;

  color: #f2f2f2;
  font-family: "Quicksand", sans-serif;
  font-size: 1em;
  font-weight: 700;
  text-transform: uppercase;

  transform-origin: 100% 50%;
}
@media (max-width: 770px) {
  #dot-nav {
    display: none;
  }
}
